.buttonDivContainer {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}

.buttonDivContainer button {
    cursor: pointer;
    border: none;
    background-color: #FFFFFF;
}

.buttonDivContainer svg {
    width: 50px;
    height: 50px;
    color: #ffbb33;
}